import React from 'react';
import { useState } from 'react'


import Header from './Header'
import Tasks from './Tasks'
import AddTask from './AddTask'

function App() {
  const [showAddTask, setShowAdd] = useState(false)
  const [tasks, setTasks] = useState([
    {
      id: 1,
      text: 'Cereal',
      day: 'Tomorrow',
      reminder: true,
    },
    {
      id: 2,
      text: 'Chicken',
      day: 'Today',
      reminder: true,
    },
    {
      id: 4,
      text: 'Cereal',
      day: 'December 13, 2021',
      reminder: false,
    },
  ]);

  //Delete Task
  const deleteTask = (id) => {
    setTasks(tasks.filter((task) => task.id !== id));
  }

  //Add Task
  const addTask = (task) => {
    const id = Math.floor(Math.random() * 1000) + 1
    const newTask = { id, ...task }
    setTasks([...tasks, newTask])
  }
  //Toggle Reminder
  const reminder = (id) => {
    setTasks(tasks.map((task) => task.id === id ? { ...task, reminder: !task.reminder } : task))
  }




  return (
    <div className='container'>
      <Header onAdd={() => setShowAdd(!showAddTask)} showAdd={showAddTask}/>
      {showAddTask && <AddTask onAdd={addTask} />}
      {tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={reminder} /> : "No Tasks to Show"}
    </div>
  );
}

export default App;